    }
    .contentpart {
        width: 90%;
        height: 100%;
    }
    .Form .Greeting {
        max-width: 200px;
        overflow: hidden;
    }
    .Form {
        width: 90%;
        height: 100vh;
        box-sizing: border-box;
        overflow: hidden;
        padding: 20px 30px;
        background: rgba(255, 255, 255, 0.123);
    }
    .input h1 {
        text-align: center;
        padding: 2%;
